import { LBP_INFO } from '../const'

const initState = {
  info: {
    name: 'BLACK LBP',
    balance: '0',
    price: '',
    start_at: '1619614800',
    end_at: '1619701200',
    status: '0',
    ratio: '', // 
    currency: {
      address: '0x0', // 0x0ht
      decimal: 18,
      symbol: 'ETH',
    },
    underlying: {
      symbol: 'BLACK',
    },
  },
}

const reduces = {
  // 
  [LBP_INFO](state, payload) {
    return { ...state, info: payload }
  },
}

export default (state = initState, action) => {
  let { type, payload } = action
  if (reduces[type]) {
    return reduces[type](state, payload)
  } else {
    return state
  }
}
